exports.push([module.id, "\n.infinite-loading-container{\n  width: 100%;\n  margin: 1rem auto 2rem;\n}\n.card-image > .image > img{\n  height: 231px;\n}\n.book-title{\n  padding: 5px 4px;\n  height: 54px;\n}\n.message-body{\n  padding: 10px;\n  margin-top: -1rem;\n}\n", "",{"version":3,"sources":["/Users/hatsu/programing/practice/vue/sokudoku-bunko/app/javascript/packs/components/app/javascript/packs/components/booksLists.vue"],"names":[],"mappings":";AA8DA;EACA,WAAA;EACA,sBAAA;AACA;AACA;EACA,aAAA;AACA;AAEA;EACA,gBAAA;EACA,YAAA;AACA;AACA;EACA,aAAA;EACA,iBAAA;AACA","file":"booksLists.vue?vue&type=style&index=0&lang=css&","sourcesContent":["<template>\n  <div class=\"columns is-mobile is-multiline\">\n    <div v-for=\"(book, idx) in books\" class=\"column is-half\">\n      <div class=\"card\">\n        <div class=\"card-image light-border-bottom\">\n          <figure class=\"image\" v-if=\"book.rakuten_book_info\">\n            <img :src=book.rakuten_book_info.medium_image_url>\n          </figure>\n          <figure class=\"image\" v-else>\n            <img src=\"/no_image.png\">\n          </figure>\n        </div>\n        <a :href=\"'/books/'+book.id\" class=\"content book-info\">\n          <div class=\"book-title\">\n            {{ book.title }}\n          </div>\n        </a>\n      </div>\n    </div>\n    <infinite-loading spinner=\"waveDots\" @infinite=\"infiniteHandler\">\n      <!-- slot -->\n      <div slot=\"no-more\"></div>\n      <div slot=\"no-results\"></div>\n    </infinite-loading>\n  </div>\n</template>\n\n<script>\nimport axios from 'axios'\nimport InfiniteLoading from \"vue-infinite-loading\";\n\nexport default {\n  props: ['path'],\n  components: {\n    InfiniteLoading\n  },\n  data: function () {\n    return {\n      books: [],\n      page: 1\n    }\n  },\n  methods: {\n    async infiniteHandler($state) {\n      await axios.get(`${this.path}&page=${this.page}`).then((response) => {\n        if(response.data.books.length > 0) {\n          this.books.push(...response.data.books)\n          this.page++\n          $state.loaded()\n        } else {\n          $state.complete();\n        }\n      }, (error) => {\n        console.log(error);\n        $state.complete();\n      });\n    },\n  },\n}\n</script>\n\n<style>\n.infinite-loading-container{\n  width: 100%;\n  margin: 1rem auto 2rem;\n}\n.card-image > .image > img{\n  height: 231px;\n}\n\n.book-title{\n  padding: 5px 4px;\n  height: 54px;\n}\n.message-body{\n  padding: 10px;\n  margin-top: -1rem;\n}\n</style>\n"]}]);

